import './CustomBMS.scss'

import Panel from 'bemuse/ui/Panel'
import React from 'react'
import PropTypes from 'prop-types'
import c from 'classnames'
import { compose } from 'recompose'
import { connect } from 'react-redux'

import * as Analytics from '../analytics'
import * as CustomSongsIO from '../io/CustomSongsIO'
import * as ReduxState from '../redux/ReduxState'
import connectIO from '../../impure-react/connectIO'

const enhance = compose(
  connect(state => ({
    log: ReduxState.selectCustomSongLoaderLog(state),
  })),
  connectIO({
    onFileDrop: () => event => CustomSongsIO.handleCustomSongFolderDrop(event),
    onPaste: () => e => CustomSongsIO.handleClipboardPaste(e),
  })
)

class CustomBMS extends React.Component {
  static propTypes = {
    log: PropTypes.arrayOf(PropTypes.string),
    onFileDrop: PropTypes.func,
    onPaste: PropTypes.func,
    onSongLoaded: PropTypes.func,
  }

  constructor(props) {
    super(props)
    this.state = { hover: false }
  }

  componentDidMount() {
    window.addEventListener('paste', this.handlePaste)
  }

  componentWillUnmount() {
    window.removeEventListener('paste', this.handlePaste)
  }

  render() {
    return (
      <Panel className='CustomBMS' title='Load Custom BMS'>
        <div className='CustomBMSwrapper'>
          <div className='CustomBMSinstruction'>
            Please drag and drop a BMS folder into the drop zone below.
          </div>
          <div className='CustomBMSremark'>
            This feature is only supported in Google Chrome and Firefox.
          </div>
          <div className='CustomBMSremark'>
            Please dont play unauthorized / illegally obtained BMS files.
          </div>
          <div className='CustomBMSremark'>
            Experimental: You can paste IPFS path/URL here.
          </div>
          <div
            className={c('CustomBMSdropzone', {
              'is-hover': this.state.hover,
            })}
            onDragOver={this.handleDragOver}
            onDragEnter={this.handleDragEnter}
            onDragLeave={this.handleDragLeave}
            onDrop={this.handleDrop}
          >
            {this.props.log ? (
              this.props.log.length ? (
                <div className='CustomBMSlog'>
                  {this.props.log.map((text, i) => (
                    <p key={i}>{text}</p>
                  ))}
                </div>
              ) : (
                <div className='CustomBMSlog'>
                  <p>Omachi kudasai...</p>
                </div>
              )
            ) : (
              <div className='CustomBMSdropzoneHint'>
                Drop BMS folder here.
              </div>
            )}
          </div>
        </div>
      </Panel>
    )
  }

  handleDragEnter = e => {
    e.preventDefault()
  }

  handleDragOver = e => {
    this.setState({ hover: true })
    e.preventDefault()
  }

  handleDragLeave = e => {
    this.setState({ hover: false })
    e.preventDefault()
  }

  handleDrop = e => {
    this.setState({ hover: false })
    Analytics.send('CustomBMS', 'drop')
    e.preventDefault()
    const promise = this.props.onFileDrop(e.nativeEvent)
    promise.then(song => {
      if (this.props.onSongLoaded) this.props.onSongLoaded(song)
    })
  }

  handlePaste = async e => {
    const song = await this.props.onPaste(e)
    if (song) {
      if (this.props.onSongLoaded) this.props.onSongLoaded(song)
    }
  }
}

export default enhance(CustomBMS)
