import _ from 'lodash'
import Bacon from 'baconjs'
import Immutable from 'immutable'

import * as DataStore from './data-store'
import * as Level from './level'
import id from './id'
import {
  INITIAL_OPERATION_STATE,
  completedStateTransition,
  isWaiting,
  operationState,
  transitionFromPromise,
} from './operations'

export function Online(service) {
  const user = new Bacon.Bus()
  const seen = new Bacon.Bus()
  const submitted = new Bacon.Bus()

  const user = user
    // https://github.com/baconjs/bacon.js/issues/536
    .toProperty(null)
    .map(user => user || service.getCurrentUser())

  function signUp(options) {
    return Promise.resolve(service.signUp(options)).tap(user =>
      user.push(user)
    )
  }

  function logIn(options) {
    return Promise.resolve(service.logIn(options)).tap(user =>
      user.push(user)
    )
  }

  function logOut() {
    return service.logOut().tap(() => user.push(null))
  }

  function submitScore(info) {
    return service.submitScore(info).tap(record => submitted.push(record))
  }

  function getScoreboard(level) {
    return service.retrieveScoreboard(level)
  }

  const allSeen = allSeenForJustSeen(seen)
  const records = user
    .flatMapLatest(recordsForUser)
    .toProperty(DataStore.INITIAL_STATE)

  const dispose = records.onValue(() => {})

  function allSeenForJustSeen(justSeen) {
    return justSeen
      .bufferWithTime(138)
      .scan(new Immutable.Map(), (map, seen) =>
        map.merge(_.zipObject(seen.map(id), seen))
      )
      .map(map => map.valueSeq())
      .skipDuplicates(Immutable.is)
      .map(seq => seq.toJS())
  }

  function recordsForUser(user) {
    let seen = {}

    {
      const action = Bacon.mergeAll(
        // Need to convert a property to EventStream to work around the
        // first-subscriber-only problem.
        allSeen
          .toEventStream()
          .delay(0)
          .flatMap(fetch),
        submitted.map(record =>
          DataStore.put(id(record), completedStateTransition(record))
        )
      )
      return DataStore.store(action)
    }

    function fetch(levels) {
      const levelsToFetch = levels.filter(level => !seen[id(level)])
      for (let level of levelsToFetch) {
        seen[id(level)] = true
      }
      const promise =
        user && levelsToFetch.length > 0
          ? service.retrieveMultipleRecords(levelsToFetch)
          : Promise.resolve([])
      return Bacon.fromPromise(
        promise
          .then(function(results) {
            let loadedRecords = _.zipObject(
              results.map(id),
              results.map(completedStateTransition)
            )
            let nullResults = _.zipObject(
              levelsToFetch.map(id),
              levelsToFetch.map(() => completedStateTransition(null))
            )
            let transitions = _.defaults(loadedRecords, nullResults)
            return DataStore.putMultiple(transitions)
          })
          .catch(function(e) {
            console.error('Cannot fetch levels:', e)
            return DataStore.putMultiple({})
          })
      )
    }
  }

  function Ranking(data) {
    const level = Level.fromObject(data)
    const retrySelf = new Bacon.Bus()
    const retryScoreboard = new Bacon.Bus()

    {
      const rankingModel = user.map(rankingModelForUser)
      const self = rankingModel
        .flatMapLatest(model => model.self)
        .toProperty(INITIAL_OPERATION_STATE)
      const scoreboardTrigger = rankingModel.flatMapLatest(
        model => model.scoreboardTrigger
      )
      const scoreboard = getScoreboardState(scoreboardTrigger)
      const state = Bacon.combineTemplate({
        self: self,
        scoreboard: scoreboard,
      }).map(conformState)
      return {
        state,
        resubmit: () => retrySelf.push(),
        reloadScoreboard: () => retryScoreboard.push(),
      }
    }

    // Make the state conform the old API. We should remove this in the future.
    function conformState(state) {
      return {
        data: state.scoreboard.value && state.scoreboard.value.data,
        meta: {
          scoreboard: _.omit(state.scoreboard, 'value'),
          submission: state.self,
        },
      }
    }

    function rankingModelForUser(user) {
      if (!user) return unauthenticatedRankingModel()
      return data.score ? submissionModel(user) : viewRecordModel(user)
    }

    function unauthenticatedRankingModel() {
      return {
        self: Bacon.constant({
          status: 'unauthenticated',
          error: null,
          record: null,
        }),
        scoreboardTrigger: Bacon.once({ force: false }),
      }
    }

    function submissionModel(user) {
      const self = submitScoreState(user)
      const selfDone = self
        .toEventStream()
        .filter(state => !isWaiting(state))
      return {
        self,
        scoreboardTrigger: selfDone.map(() => ({ force: true })),
      }
    }

    function viewRecordModel(user) {
      return {
        self: getRecordState(user),
        scoreboardTrigger: asap({ force: false }),
      }
    }

    function getScoreboardState(trigger) {
      return operationState(
        trigger
          .merge(retryScoreboard)
          .flatMapLatest(() => transitionFromPromise(getScoreboard(level)))
      )
    }

    function getRecordState(user) {
      return operationState(
        asap()
          .merge(retrySelf)
          .flatMapLatest(() =>
            transitionFromPromise(service.retrieveRecord(level, user))
          )
      )
    }

    function submitScoreState(user) {
      return operationState(
        asap()
          .merge(retrySelf)
          .flatMapLatest(() => transitionFromPromise(submitScore(data)))
      )
    }
  }

  function seen(level) {
    return seen.push(level)
  }

  function asap(value) {
    return Bacon.later(0, value)
  }

  return {
    user,
    records,
    signUp,
    logIn,
    logOut,
    submitScore,
    scoreboard: getScoreboard,
    Ranking,
    seen,
    dispose,
  }
}

export default Online
